.img {
  width: 250px;
  margin: auto;
  margin-top: 100px;
}
.text {
  margin-top: var(--spacing-large);
  font-size: var(--font-size-subheader);
  color: var(--color-white);
  text-align: center;
  text-shadow: var(--text-shadow-blue);
}

@media screen and (max-width: 479px) {
  .img {
    width: 180px;
    margin-top: 50px;
  }
}
